/* Section and page margin definitions */
.page-margin,
.shopify-challenge__container {
  margin: var(--hh-section-vertical-margin) auto;
}

.spaced-section {
  margin-top: var(--hh-section-vertical-margin);
}

@media screen and (max-width: 749px) {
  .spaced-section--md {
    margin-top: 0;
  }
}

.spaced-section:last-child {
  margin-bottom: var(--hh-section-vertical-margin);
}

.spaced-section--full-width + .spaced-section--full-width {
  margin-top: 0;
}

.spaced-section--full-width:first-child {
  margin-top: 0;
}

.spaced-section--full-width:last-child {
  margin-bottom: 0;
}

/**
 * A range of margin and padding declarations
 * that can be used for simple adjustments to spacing
 * that are consistent across breakpoints.
 */
.mha,
.mxa {
  margin-left: auto;
}

.mha,
.mxa {
  margin-right: auto;
}

.mva,
.mxa {
  margin-top: auto;
}

.mva,
.mxa {
  margin-bottom: auto;
}

